import React from 'react';
import emailjs from 'emailjs-com';
import { Form, Col} from 'react-bootstrap';
import ContactModal from './ContactModal';


function ContactUs() {

    function sendEmail(e) {
        e.preventDefault();

        emailjs.sendForm('brsv_service', 'contact_form', e.target, 'user_vCpY4Np0oCQgl7jxezsd0')
            .then((result) => {
                console.log(result.text);
            }, (error) => {
                console.log(error.text);
            });
        e.target.reset()
    }

    return (
        <Form onSubmit={sendEmail}>
            <Form.Row>
                <Form.Group as={Col} controlId="formGridEmail">
                    <Form.Label>Email</Form.Label>
                    <Form.Control type="email" placeholder="Enter email" name="user_email" />
                </Form.Group>
                <Form.Group as={Col} controlId="formGridEmail">
                    <Form.Label>Name</Form.Label>
                    <Form.Control type="text" placeholder="Enter name" name="user_name" />
                </Form.Group>
            </Form.Row>

            <Form.Group controlId="formGridPhone">
                <Form.Label>PhoneNumber</Form.Label>
                <Form.Control placeholder="EnterNumber" type="number" name="user_number" />
            </Form.Group>

            <Form.Row>
                <Form.Group as={Col} controlId="formGridCity">
                    <Form.Label>City</Form.Label>
                    <Form.Control type="text" placeholder="Enter city" name="user_city" />
                </Form.Group>

                <Form.Group as={Col} controlId="formGridZip">
                    <Form.Label>Comment</Form.Label>
                    <Form.Control as="textarea" placeholder="Leave a comment here" name="message" />
                </Form.Group>
            </Form.Row>

            {/* <Form.Group id="formGridCheckbox">
                    <Form.Check type="checkbox" label="Check me out" />
                </Form.Group> */}

            <ContactModal />
        </Form>
    );
}

export default ContactUs;